body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	color: #333;
	font-size: 1em;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}

td,
th { 
	overflow: hidden;
	text-align: left;
	vertical-align: middle;
}

th a,
td a { 
	display: block;
	width: 100%;
}

th a:link,
th a:visited,
th a:hover,
th a:active {
	color: #000;
	text-decoration: none;
}

a.sort-by { 
  cursor: pointer;
	padding-right: 18px;
	position: relative;
}

a.sort-by:before,
a.sort-by:after {
	border: 4px solid transparent;
	content: "";
	display: block;
	height: 0;
	right: 5px;
	top: 50%;
	position: absolute;
	width: 0;
}

a.sort-by:before {
	border-bottom-color: #666;
	margin-top: -9px;
}

a.sort-by:after {
	border-top-color: #666;
	margin-top: 1px;
}

td, th {
  border-bottom: 1px solid rgba(224, 224, 224, 1);
  overflow: hidden;
  padding: 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

th.row-details {
  width: 60%;
}

th.row-flight-number {
  width: 160px;
}

tr {
  height: 50px;
  border-collapse: collapse;
  border-spacing: 0;
}

tr.clickable-row {
  cursor: pointer;
}

tfoot .pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end
}

button {
  cursor: pointer;
  margin: 0 16px;
  padding: 8px 16px;
}

button:disabled {
  cursor: not-allowed;
}

